$mla-green: #004a51;
$style-purple: #604;

$style-light-blue: #2e67b1;

$mla-sans: proxima-nova, 'proxima nova', 'Helvetica Neue', Helvetica, sans-serif;
$mla-serif: lexia, Garamond, 'Adobe Garamond Pro', Minion, serif;



// Citation styles
$author-orange: #fabf7c;
$border-orange: #fabf7c;
$container-one-blue: #bce1fa;
$container-two-blue: #9bc4e0;
$container-three-blue: #4d9ed6;
$optional-elemement-yellow: #fcf9cd;

body.page-template-practice-template {
  main.content {
    max-width: 1200px;

    div.block-main {
      flex-basis: 55%;
    }

    aside.citation-sidebar {
      flex-basis: 35%;
    }
  }
}


article.practice-template {
  fieldset {
    margin: 1.5rem 0 1.25rem;
    padding: 0;
    border-radius: 6px;
    //border: 1px solid #309;
    border: none;
    //margin-top: 1.25rem;
    //margin-bottom: 1.25rem;

    @media print {
      margin-top: 0rem;
      margin-bottom: 0;
      padding-top: .1rem;
      padding-bottom: .1rem;
    }

    legend {
      font-family: $mla-sans;
      color: $style-light-blue;
      font-weight: bold;
      text-transform: uppercase;
      letter-spacing: 0.15rem;
      display: none;

      &.legend-visible {
        display: block;
        margin-top: 1.5rem;
      }

      @media print {
        //margin-bottom: -.2rem;
      }
    }
  }

  .optional-element {
      //background: lighten(#309, 60%);
      border: none;
      padding: 0;
      box-sizing: border-box;
      position: relative;
      display: flex;
      padding-left: 1.05rem;

      .label {
        text-transform: uppercase;
        font-size: 0.8rem;
        letter-spacing: 0.15rem;
        margin-top: 0rem;

        //@media print {
        //  font-size: .25rem;
        //}

      }

      &.temp-element {
        //background: rgba(#339,0.05);
        .label {
          text-transform: none;
          letter-spacing: 0;
          margin-top: 0.35rem;

          &.focused {
            font-size: 0.8rem;
            margin: -.15rem 0 0 0.5rem;
            transition: all 0.2s;
          }

          @media print {
            font-size: 0.1rem;
            font-weight: normal;
            margin: -.15rem 0 0 0 0;
          }
        }
      }



      .element-order {
        height: 1.25rem;
        line-height: 1.25rem;
        background: $style-light-blue;
        padding: .075rem;
        width: 1.25rem;
        border-radius: 2.5rem;
        text-align: center;
        color: #fff;
        font-family: $mla-sans;
        display: inline-block;
        box-sizing: border-box;
        cursor: pointer;

        @media print {
          //width: .5rem;
          //height: .5rem;
        }
      }

      @media print {
        display: none;
      }


    }

  .temp-element {

    border: 1px solid $style-light-blue;
    border-radius: 4px;
    margin: .75rem auto;
    padding: .35rem 0 0.35rem 0.35rem;
    box-sizing: border-box;
    position: relative;
    display: flex;
    transition: all .2s;

    .element-order {
      height: 100%;
      background: $style-light-blue;
      padding: .5rem .25rem;
      width: 2rem;
      border-radius: 3px;
      text-align: center;
      color: #fff;
      font-family: $mla-sans;
      display: inline-block;
      box-sizing: border-box;
      transition: all .2s;
      cursor: auto;

      @media print {
        padding: .15rem .1rem;
        //width: 1rem;
        font-size: 0.8rem;
      }
    }

  }







  .label {
    display: inline-block;
    font-family: $mla-sans;
    font-weight: lighter;
    font-size: 1.25rem;
    position: absolute;
    left: 2.5rem;
    margin: 0.35rem 0 0 0.5rem;
    transition: all 0.2s;
  //  z-index: 1;

    @media print {
      font-size: 0.15rem !important;
      margin: -.15rem 0 0 0.25rem;
    }
  }

  div.formatting-btn {
    margin: 0.1rem .35rem 0 auto;
    height: 100%;
    font-size: 1rem;
    line-height: 1.5rem;
    padding: 0.35rem 0.25rem .3rem;
    border-radius: 2px;
    box-sizing: border-box;
    background: $mla-green;
    color: #ffffff;
    cursor: pointer;
    transition: opacity .2s;
    opacity: 1;

    &.hidden {
      opacity: 0;
      transition: opacity .2s;
    }

    @media print {
      display: none;
    }
  }



  div.formatting-btn {
    &:hover {
      background: lighten($mla-green, 10%);
    }

    &:active {
      background: lighten($mla-green, 40%);
    }

    &.active {
      background: lighten($mla-green, 30%);
    }

  }

  .input {
    //height: 90%;
    padding: 1.2rem 0 0 .5rem;
    width: calc( 100% - 105px );
    display: inline-block;
    box-sizing: border-box;
    //position: absolute;
    bottom: .15rem;
    line-height: 100%;
    font-size: 1.3rem;
    z-index: 1;
    left: 2.5rem;
    font-family: $mla-serif;
    font-weight: 300;

    br {
      display: none;
    }

    @media print {
      font-size: .7rem;
      padding: 0.7rem 0 0 .5rem;
      left: 2rem;

    }

    &:focus {

      outline: none;
      border: none;


      + .label {
        font-size: .8rem;
        margin: -.15rem 0 0 0.5rem;
        transition: all 0.2s;

        @media print {
        font-size: .1rem;
        }
      }

    }
  }

  .focused {
    font-size: 0.8rem;
    margin: -.15rem 0 0 0.5rem;
    transition: all 0.2s;
  }

  .button {
    background: $style-purple;
    color: #fff;
    font-family: $mla-sans;
    padding: 1rem;
    text-align: center;
    display: block;
    font-size: 1.2rem;
    cursor: pointer;
    border: none;

    &:hover {
      background: lighten($style-purple, 10%);
    }

    @media print {
      display: none;
    }
  }

  @media print {
     .temp-element {
       padding: .25rem;
     }


  }
}

@media print {
  .practice-template-content {
    display: none;
  }
}

.clear-button {
  text-align: right;
  text-decoration: underline;
  color: #06c;
  font-size: 1.15rem;
  cursor: pointer;
}


// Citation example and key styles

.info-box {
  border-radius: 3px;
  padding: .5rem;
  border: 1px solid $style-light-blue;
}

.citation-key {
  margin-top: 2rem;
}

h6.info-box--label {
  font-weight: 700;
  font-size: 1.1rem;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  color: $style-light-blue;
}

.citation-example {
  font-size: 1.25rem;
  padding-left: 1.25rem;
  text-indent: -1.25rem;
  margin-bottom: 0;

  &.citation-example--small {
    font-size: 0.9rem;

    span:hover {
      box-shadow: none;
    }
  }

  &::first-line {
   // padding-left: 0;
  }

  br {
    display: none;
  }

  span {
    padding: 3px 4px 0 4px;
    line-height: 1.75em;

    // The code below adds a space after each entry,
    // but keeps the span empty as needed.
    &:after {
      //content: "\0020";
      white-space: pre;
    }

    &:empty {
      padding-left: 0;
      padding-right: 0;

      &:after {
        content: none;
      }
    }

    &:not(:empty) {
       border: 2px solid;
    }

    &:not(:empty):last-of-type {
      padding-right: 4px;

    }

    &.citation__author {
      background: $author-orange;
      border-color: $author-orange
    }

    &.citation__title {
      background: $author-orange;
      border-color: $author-orange;
    }

    &.citation__container--one {
      background: $container-one-blue;
      border-color: $container-one-blue;
    }

    &.citation__container--two {
      background: $container-two-blue;
      border-color: $container-two-blue;
    }

    &.citation__container--three {
      background: $container-three-blue;
      border-color: $container-three-blue;
    }

    &.citation__optional-element:not(:empty) {
      background: $optional-elemement-yellow;
      border: 2px solid $author-orange;
    }

    &:hover {
      cursor: default;
      box-shadow: 2px 2px 6px #b7b8b9;
    }
  }



}

.hovered {
      background: #f3f6f9;
      box-shadow: 2px 2px 12px #cccecf;

    &.author-element, &.title-element {
      background-color: rgba($author-orange, 0.3);
    }

    &.optional-element {
      background-color: rgba($optional-elemement-yellow, 0.4);
    }
}

fieldset.container:nth-child(2) .container-element.hovered {
      background-color: rgba($container-one-blue, 0.4);
    }

fieldset.container:nth-child(3) .container-element.hovered {
  background-color: rgba($container-two-blue, 0.5);
}

fieldset:nth-child(4) .container-element.hovered {
  background-color: rgba($container-three-blue, 0.6);
}



.citation-sidebar--sticky-block {
  position: sticky;
  position: -webkit-sticky;
  top: 4rem;
  margin-top: 5rem;

  @media screen and (max-width: 767px) {
    margin-top: 1rem;
    top: 0;
    position: relative;
  }
}

.handbook-ad-container {
  margin: 3rem auto 2rem;
  width: 75%;

  @media screen and (max-width: 1165px) {
    width: 90%;
  }

  @media screen and (max-width: 900px) {
    width: 100%;
  }

  .button-group {
    position: relative;
  }
}



